import { baseFilters } from './mixins/contentMixin'
import BaseButton from './components/BaseButton'
import BaseCard from './components/BaseCard'
import BasePropChip from './components/BasePropChip'
import BasePropDisplay from './components/BasePropDisplay'
import BaseTitleDisplay from './components/BaseTitleDisplay'
import MarkerExternal from './components/MarkerExternal'

if (typeof window !== 'undefined' && window.Vue) {
  window.Vue.component('ArticleCard', component.exports)
}
export default {
  components: {
    BaseButton,
    BaseCard,
    BasePropChip,
    BasePropDisplay,
    BaseTitleDisplay,
    MarkerExternal
  },
  mixins: [baseFilters],
  props: {
    item: Object,
    onTagClick: Function,
    preview: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {
      showAbstract: false
    }
  },
  computed: {
    article() {
      return this.item
    },
    abstractIcon() {
      return this.showAbstract ? 'mdi-chevron-down' : 'mdi-chevron-up'
    },
    cardHeight() {
      console.log(this.$refs)
      return this.$refs.card.$el.clientHeight
    }
  }
}
