if (typeof window !== 'undefined' && window.Vue) {
  window.Vue.component('BaseCard', component.exports)
}
export default {
  props: {
    external: {
      type: Boolean,
      default: false
    },
    project: {
      type: Boolean,
      default: false
    }
  },
  computed: {
    color() {
      if (this.external) return 'rgb(255, 250, 245)'
      else if (this.project) return `rgb(255, 253, 247)`
      else return ''
    }
  }
}
