if (typeof window !== 'undefined' && window.Vue) {
  window.Vue.component('BaseToolbar', component.exports)
}
export default {
  props: {
    menu: {
      type: Boolean,
      default: true
    },
    logoPath: {
      type: String,
      default: ''
    }
  },
  data() {
    return {
      drawer: null,
      height: 60
    }
  },
  computed: {
    logoHeight() {
      return this.height * 0.75
    },
    logoSrc() {
      return this.logoPath ? this.logoPath : process.env.VUE_APP_LOGO_URL
    }
  }
}
